body {
  color: #5c5a63;
  font-family: Source Sans Pro, HelveticaNeue-Light, Helvetica Neue Light,
    Helvetica Neue, Helvetica, Arial, Lucida Grande, sans-serif;
  font-size: 1.5rem;
  line-height: 1.6em;
  font-weight: 300;
  font-style: normal;
  letter-spacing: 0;
  text-rendering: optimizeLegibility;
  background: #fff;
  -moz-font-feature-settings: "liga" on;
  font-feature-settings: "liga" on;
}

.disable-bootstrap-link a:hover {
  text-decoration: none !important;
  color: inherit !important;
}

.disable-bootstrap-link a {
  color: inherit !important;
}

.logo {
  width: 80px;
}

.nav-head {
  background-color: #fff;
  width: 100%;
}
.site-nav-right {
  justify-content: center;
  display: flex;
  align-items: center;
}

.site-nav-right .sign-in {
  opacity: 0.6;
  padding-right: 10px;
  transition: 0.3s ease-in;
  color: #000;
  opacity: 0.6;
  font-size: 0.9rem;
  font-weight: 400;
}

.site-nav-right .button {
  padding: 0 10px;
  min-width: 122px;
  text-align: center;
  line-height: 40px;
  text-transform: uppercase;
  font-weight: 400;
  transition: 0.3s ease;
}
.button-red {
  font-size: 0.8rem;
  background-color: #f63954;
  border-color: #f63954;
  color: #fff;
}

.site-nav-right a:hover {
  opacity: 1;
}
.site-nav-right .button:hover {
  transform: translateX(10px);
  text-decoration: none;
  color: #fff;
}

.black {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 500px;
  width: 100%;
  background-color: #181818;
  color: #fff;
}
.black img {
  max-height: 80px;
}
.logo-title {
  font-size: 1.5rem;
  font-weight: 300;
  letter-spacing: 0.5px;
}
.top-post {
  background-color: #fff;
  box-shadow: 8px 14px 38px rgb(39 44 49 / 6%), 1px 3px 8px rgb(39 44 49 / 3%);
  transition: all 0.5s ease;
  margin: -100px 0 30px;
}
.top-post:hover {
  box-shadow: 8px 28px 50px rgb(39 44 49 / 7%), 1px 6px 12px rgb(39 44 49 / 4%);
  transition: all 0.4s ease;
  transform: translate3D(0, -1px, 0) scale(1.02);
  cursor: pointer;
}
@media only screen and (min-width: 992px) {
  .top-post-image {
    display: flex !important;
    flex-direction: row !important;
  }
  .top-post-image img {
    width: 700px !important;
  }
}
.top-post-image {
  border: none !important;
  background-color: transparent !important;
}
.top-post-image img {
  border: none !important;
  border-top-left-radius: 0 !important;
  border-top-right-radius: 0 !important;
}
.card-post {
  text-align: left;
  box-shadow: 8px 14px 38px rgb(39 44 49 / 6%), 1px 3px 8px rgb(39 44 49 / 3%);
  transition: all 0.5s ease;
  border: none !important;
}
.card-post:hover {
  box-shadow: 8px 28px 50px rgb(39 44 49 / 7%), 1px 6px 12px rgb(39 44 49 / 4%);
  transition: all 0.4s ease;
  transform: translate3D(0, -1px, 0) scale(1.02);
  cursor: pointer;
}
.card-post-inner {
  margin-bottom: 50px;
}
/* footer {
   background-color: #181818; 
} */
footer .footer {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login {
  width: calc(100% - 24px);
  max-width: 1036px;
  padding: 0 12px;
  margin: 0 auto;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.login-title {
  font-family: Open Sans, Helvetica Neue, Helvetica, Arial, sans-serif;
  color: #34323b;
  letter-spacing: -1px;
  font-weight: 800;
  font-size: 36px;
  line-height: 49px;
}
.login-inputs {
  height: 500px;
  width: 500px;
  margin: 20px;
}
.login-input {
  margin-bottom: 20px;
}
.login-input input {
  font-size: 18px;
  color: #34323b;
  line-height: 30px;
  position: relative;
  display: table-cell;
  margin: 0;
  outline: none;
  width: 100%;
  height: 70px;
  padding: 8px 18px;
  border: 1px solid #e6e6ed;
  box-sizing: border-box;
  z-index: 2;
}
.login-input-label {
  margin-bottom: 5px;
  font-family: Source Sans Pro, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: 400;
  color: #34323b;
  font-size: 20px;
}
.asterisk {
  color: #f63954;
  margin-left: 4px;
}
.global-button {
  width: 100%;
  background-color: #f63954;
  border-color: #f63954;
  color: #fff;
  font-size: 16px;
  padding: 14px 24px;
  border: 2px solid transparent;
  text-align: center;
  outline: none;
  margin: 2px auto;
  padding: 10px 19px;
  cursor: pointer;
  white-space: nowrap;
  transition: all 0.3s ease-out;
}
.global-button:hover {
  background-color: #e80b2a;
  border-color: #e80b2a;
}

.forget-password a:hover {
  color: #f63954 !important;
}
@media (min-width: 1280px) {
  .inner {
    padding: 0 40px;
  }
}
.inner {
  margin: 0 auto;
  margin-top: 100px;
  max-width: 1170px;
  width: calc(100% - 100px);
  padding: 0;
}
.post-full-title {
  color: #27262c;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 50px;
  font-size: 3rem;
}
.post-full-image {
  margin: 0;
  height: 490px;
  background: #c5d2d9 50%;
  background-size: cover;
  border-radius: 5px;
}
.post-full-content {
  position: relative;
  margin: 0 auto;
  /* padding: 63px 190px 40px; */
  padding-top: 20px;
  min-height: 230px;
  font-family: Source Sans Pro, Helvetica, Arial, Lucida Grande, sans-serif;
  font-size: 2rem;
  line-height: 1.6em;
  background: #fff;
}
.post-content ol,
.post-content p,
.post-content ul {
  font-size: 23.2px;
  line-height: 36.4px;
  font-weight: 300;
  text-align: left;
}
.post-full-content .kg-width-wide .kg-image {
  max-width: 1040px;
  width: 100%;
}
.kg-gallery-image img {
  max-width: 1040px;
  height: 100%;
  width: 100%;
}
.post-full-content figcaption {
  margin: 1em 0 0;
  font-size: 50%;
  line-height: 1.6em;
  letter-spacing: 2px;
}
.post-full-content figure {
  margin: 1.5em 0 3em;
}
.author-card,
.post-full-footer {
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  margin: 50px 0;
}
.author-card .author-profile-image,
.author-card .avatar-wrapper {
  position: relative;
  margin-right: 28px;
}

.author-card .author-profile-image,
.author-card .avatar-wrapper {
  margin-right: 25px;
  width: 60px;
  height: 60px;
}
.author-profile-image,
.avatar-wrapper {
  display: block;
  width: 100%;
  height: 100%;
  background: #e3e9ed;
  border-radius: 100%;
  object-fit: cover;
}
.author-card-name h4 {
  margin: 8px 0 2px;
  padding: 0;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 600;
}
.author-card-name a {
  color: #34323b;
}
